import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ManagerListComponent } from './manager-list.component';
import { ManagerListRoutingModule } from './manager-list-routing.module';
import { PageHeaderModule } from '../../shared';
import { MatInputModule, MatButtonModule, MatSelectModule, MatCardModule, MatPaginatorModule, MatTableModule, MatSortModule, MatCheckboxModule, MatTooltipModule, MatSlideToggleModule } from '@angular/material';
import { FormsModule, ReactiveFormsModule,  } from '@angular/forms';
import { MatFormFieldModule } from '@angular/material';
import { MatStepperModule } from '@angular/material/stepper';
import {ToasterModule } from 'angular5-toaster';

@NgModule({
  imports: [
    CommonModule,
    ManagerListRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    PageHeaderModule,
    MatStepperModule,
    MatFormFieldModule,
    MatCardModule,
    MatCheckboxModule,
    MatInputModule,
    MatSelectModule,
    MatButtonModule,
    MatPaginatorModule,
    MatTableModule,
    MatSortModule,
    MatTooltipModule,
    ToasterModule,
    MatSlideToggleModule

  ],
  declarations: [ManagerListComponent]
})
export class ManagerListModule { }



